                cityId[2] = 101280102;
                cityId[3] = 101280201;
                cityId[4] = 101280401;
                cityId[5] = 101281401;
                cityId[6] = 101280601;
                cityId[7] = 101281301;
                cityId[8] = 101280501;
                cityId[9] = 101282101;
                for (let i = 0; i < 10; i++) {
                    this.axios.get(string1 + cityId[i] + string2).then((res) => {
                        this.message[i] = "平均温度：" + res.data.data["0"].tem + " \n 天气：" + res.data.data["0"].wea;
                        document.getElementById(i).innerText = this.message[i];
                        // eslint-disable-next-line no-console
                        console.log(this.message[i]);
                    })
                }
            }
        }
    }
